a {
  text-decoration: none; }

li {
  list-style: none; }

@font-face {
  font-family: "Raleway Regular";
  src: url("/src/assets/fonts/raleway/raleway-regular.ttf") format("truetype"); }

@font-face {
  font-family: "Raleway Bold";
  src: url("/src/assets/fonts/raleway/raleway-bold.ttf") format("truetype"); }

.l-container {
  display: flex;
  flex-direction: column;
  max-width: 1136px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box; }

body {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #202835;
  font-family: 'Raleway Regular', sans-serif;
  color: #FFFFFF;
  font-weight: 700; }

.l-about__area {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 0; }

.l-perfil__area {
  display: flex;
  align-items: center;
  max-width: 250px;
  width: 100%;
  justify-content: center; }

.l-card__hobbies {
  display: flex;
  flex-direction: row;
  height: 90px;
  width: 300px;
  background-color: #293342;
  border-radius: 5px;
  justify-content: center;
  align-items: center; }

.l-carrossel {
  display: flex;
  flex-direction: row;
  height: 90px;
  width: 300px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.45);
  background-color: #293342;
  border-radius: 5px;
  align-items: center;
  overflow: hidden; }

.l-carrossel__container {
  display: flex;
  flex-direction: row;
  transition: transform 0.4s ease-in-out; }

.l-navbar {
  align-items: center;
  display: flex;
  width: 100%;
  max-width: 1136px;
  height: 5rem;
  flex-direction: row;
  justify-content: space-between; }

.c-navbar__item {
  padding: 15px;
  box-sizing: border-box; }
  .c-navbar__item:hover {
    border-bottom: 3px solid #48B0D3; }

.c-navbar__link {
  display: flex;
  flex-direction: row;
  font-weight: 700;
  color: #FFFFFF; }

.c-navbar__list {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  height: 3.5rem; }

.c-navbar__logo {
  align-items: center;
  background-color: #48B0D3;
  box-sizing: border-box;
  border-radius: 50%;
  display: flex;
  color: #FFFFFF;
  justify-content: center;
  font-weight: 700;
  padding: 0.5rem;
  height: 40px;
  width: 40px; }

.c-button {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  border: 2px solid #48B0D3;
  padding: 8px 16px;
  width: 150px;
  height: 51px;
  color: #FFFFFF; }

.c-perfil__image {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 4px solid #48B0D3;
  height: 230px;
  width: 230px;
  border-radius: 50%; }

.c-about__me {
  display: flex;
  flex-direction: column;
  width: 600px; }

.c-cite {
  display: flex;
  width: 500px;
  max-width: 100%;
  align-items: center; }

.c-card__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2px;
  height: 90px;
  width: 90px; }
